import React from "react";
import FreeBoardTableItemComp from "./tableitems/FreeBoardTableItemComp";
import { useSelector } from "react-redux";
import {Link} from "react-router-dom";
import FreeBoardTableHeaderComp from "./tableheader/FreeBoardTableHeaderComp";

function FreeBoardComp() {
  const { boardList } = useSelector((state) => state.board);
  const style = ""

  return (
    <>
      <div className="page_title">자유게시판</div>
      <div className="">
      <FreeBoardTableHeaderComp style={style}/>
          {boardList.map((item) => (
            <FreeBoardTableItemComp key={item.postNo} style={style} item={item} />
          )).reverse()}
        <Link to={"writecontent"} className={"main_menu_btn"}>
          <div className="confirm-btn">글 작성하기</div>
        </Link>
      </div>
    </>
  );
}

export default FreeBoardComp;
